
import React, { useState } from 'react'
import Card, { CardTypeEnum } from '../Card';
import useCountdown from '../../hooks/useCountdown';
import './index.less';

export default function Achievement() {
  const [end, setEnd] = useState(false);
  const { hours, minutes, seconds } = useCountdown(100000, () => { setEnd(true) })
  return <Card type={CardTypeEnum.Large}>
    <div className='achievement'>
      <div className='achievement-title'>react </div>
      <div className='achievement-content'>
        <div className='achievement-label'></div>
        <div className='achievement-value'>{`${hours}:${minutes}:${seconds}`} {end && ''}</div>
      </div>
    </div>
  </Card>
}